"use client";
import React, { useMemo, useEffect, useState } from "react";
// import Image from "next/image"; // Removed Image component reference as logo file is unavailable

/** ----------------------------------------------------------------
 *  Types & Constants
 *  ---------------------------------------------------------------- */
type GasProfileKey = "air" | "o2" | "n2";

/** รายการกำลังไฟที่กำหนดเอง */
type KwItem = {
    id: string;
    name: string;
    kw: number;
};

/** โครงสร้าง Inputs หลัก (ปรับให้ตรงตาม V19/V20) */
type Inputs = {
    // Production
    minutesPerPiece: number;
    hoursPerDay: number;
    daysPerMonth: number;
    loadFactorPct: number;

    // Electricity
    elecRateTHBPerKwh: number; // THB/kWh (V19)
    kwItems: KwItem[];

    // Cost/month (Simplified)
    sparePartsTHBPerMonth: number; // Total Spare Cost / Month (Input)

    // Gas per minute (V19)
    gasRateTHBPerMin: number; // THB/min (Used for calculation)
    activeGas: GasProfileKey; // Used for display context only
};

const genId = () => Math.random().toString(36).slice(2, 9);

/** ค่าเริ่มต้น: ใช้ค่า kW อ้างอิงจาก 3000W V19 และค่า Rate จาก V19/V20 */
const DEFAULT_KW_ITEMS: KwItem[] = [
    { id: genId(), name: "1. Laser Source (kW)", kw: 9.1 },
    { id: genId(), name: "2. Chiller (kW)", kw: 2.5 },
    { id: genId(), name: "3. Stabilizer (kW)", kw: 27 },
    { id: genId(), name: "4. Machine Type (kW)", kw: 7 },
    { id: genId(), name: "5. Air Compressor (kW)", kw: 22 },
    { id: genId(), name: "6. Dust Collector (kW)", kw: 3 },
];

const DEFAULT_INPUTS: Inputs = {
    minutesPerPiece: 1.05,
    hoursPerDay: 8,
    daysPerMonth: 22, // ค่าเริ่มต้น
    loadFactorPct: 60,

    elecRateTHBPerKwh: 4.5,
    kwItems: DEFAULT_KW_ITEMS,

    sparePartsTHBPerMonth: 0, // ตั้งต้นเป็น 0 
    gasRateTHBPerMin: 0.5762, // Oxygen default from V19
    activeGas: "o2",
};

/** Utils */
function thb(n: number) {
    if (!isFinite(n) || n === null) return "-";
    return n.toLocaleString("th-TH", { minimumFractionDigits: 2, maximumFractionDigits: 2 });
}
function num(n: number) {
    if (!isFinite(n) || n === null) return "-";
    return n.toLocaleString("th-TH", { maximumFractionDigits: 2 });
}

/** ----------------------------------------------------------------
 *  Component
 *  ---------------------------------------------------------------- */
export default function LaserOpexDashboard() {
    const [inputs, setInputs] = useState<Inputs>(DEFAULT_INPUTS);
    const [hydrated, setHydrated] = useState(false);

    // load inputs (Hydration logic for persistent state)
    useEffect(() => {
        try {
            const raw = localStorage.getItem(`laserOpex.inputs.v20`);
            if (raw) {
                const parsed = JSON.parse(raw) as Inputs;
                
                // Normalization/Migration if needed (currently minimal)
                const normalizedInputs = {
                    ...DEFAULT_INPUTS,
                    ...parsed,
                    // Ensure kwItems are present and use default if missing
                    kwItems: parsed.kwItems.length > 0 ? parsed.kwItems : DEFAULT_KW_ITEMS
                };
                setInputs(normalizedInputs);
            }
        } catch {}
        setHydrated(true);
    }, []);

    // persist inputs 
    useEffect(() => {
        if (!hydrated) return;
        try { localStorage.setItem(`laserOpex.inputs.v20`, JSON.stringify(inputs)); } catch {}
    }, [inputs, hydrated]);

    /** ---------------- Derived Calculation (Core Logic from Snippet, adapted) ---------------- */
    const derived = useMemo(() => {
        // 1. Production Parameters
        const totalKwBeforeLoad = inputs.kwItems.reduce((s, it) => s + (Number(it.kw) || 0), 0);
        
        const hoursPerMonthPossible = inputs.hoursPerDay * inputs.daysPerMonth;
        const hoursPerMonth = (hoursPerMonthPossible * inputs.loadFactorPct) / 100.0;
        
        const pphFromMins = inputs.minutesPerPiece > 0 ? 60 / inputs.minutesPerPiece : 0;
        const pph = pphFromMins; // ใช้ minutesPerPiece เป็นแหล่งกำเนิดหลัก
        const minutesPerPiece = pph > 0 ? 60 / pph : inputs.minutesPerPiece;
        const monthlyPieces = pph * hoursPerMonth;

        // 2. Electricity Cost (THB/kWh -> THB/min)
        const totalKw = totalKwBeforeLoad * (inputs.loadFactorPct / 100.0); // Apply Load Factor to total KW
        const elecPerHour = totalKw * inputs.elecRateTHBPerKwh;
        const elecPerMin = elecPerHour / 60;
        const elecPerPiece = minutesPerPiece * elecPerMin;
        const elecPerMonth = elecPerHour * hoursPerMonth;

        // 3. Gas Cost (THB/min)
        const gasPerMin = inputs.gasRateTHBPerMin;
        const gasPerPiece = minutesPerPiece * gasPerMin;
        const gasPerHour = gasPerMin * 60;
        const gasPerMonth = gasPerHour * hoursPerMonth;

        // 4. Spare Cost (THB/month -> THB/piece)
        const sparePerPiece = monthlyPieces > 0 ? inputs.sparePartsTHBPerMonth / monthlyPieces : 0;
        
        // 5. Total
        const totalPerPiece = elecPerPiece + gasPerPiece + sparePerPiece;
        const totalPerMonth = elecPerMonth + gasPerMonth + inputs.sparePartsTHBPerMonth;
        const totalPerYear = totalPerMonth * 12;

        return {
            totalKw: totalKwBeforeLoad, // แสดง KW ก่อนคูณ Load Factor
            totalKwAfterLoad: totalKw, // KW หลังคูณ Load Factor (สำหรับคำนวณต้นทุน)
            elecPerHour, elecPerMin, pph, minutesPerPiece,
            hoursPerMonth, monthlyPieces, elecPerPiece, gasPerPiece,
            sparePerPiece, totalPerPiece, elecPerMonth, gasPerMonth,
            totalPerMonth, totalPerYear,
        };
    }, [inputs]);

    /** ---------------- Handlers ---------------- */
    const update = (patch: Partial<Inputs>) => setInputs({ ...inputs, ...patch });
    
    // Handler สำหรับ KwItem list
    const updateKw = (id: string, patch: Partial<KwItem>) => {
        const next = inputs.kwItems.map((it) => (it.id === id ? { ...it, ...patch } : it));
        update({ kwItems: next });
    };

    const addKwRow = () =>
        update({
            kwItems: [
                ...inputs.kwItems,
                { id: genId(), name: "7. Custom Device (kW)", kw: 0 },
            ],
        });

    const removeKwRow = (id: string) => update({ kwItems: inputs.kwItems.filter((it) => it.id !== id) });
    
    // Handler สำหรับ Gas Type Selection
    const handleGasTypeChange = (key: GasProfileKey) => {
        let defaultRate = 0;
        if (key === "o2") defaultRate = 0.5762;
        if (key === "n2") defaultRate = 2.5495;

        update({ activeGas: key, gasRateTHBPerMin: defaultRate });
    };

    /** ---------------- UI ---------------- */
    return (
        <div className="min-h-screen bg-gray-50 text-gray-900 p-6">
            <div className="mx-auto max-w-7xl">
                <header className="mb-6 flex flex-col gap-2 md:flex-row md:items-end md:justify-between">
                    <div>
                        <h1 className="flex items-center gap-3 text-xl md:text-2xl font-bold">
                            {/* <Image src="/image/logo.webp" alt="TAKECO Logo" width={50} height={50} priority/> */}
                            <span>TAKECO LASER Operation Cost Dashboard (V20)</span>
                        </h1>

                        <p className="text-sm text-gray-600">คำนวณต้นทุนต่อชิ้น / ต่อเดือน / จากพารามิเตอร์การผลิตจริง</p>
                    </div>
                </header>
                
                <hr className="my-4"/>

                {/* Grid */}
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    {/* Left: Inputs */}
                    <section className="lg:col-span-2 space-y-6">
                        
                        {/* KW Items */}
                        <div className="bg-white rounded-2xl shadow p-5">
                            <h2 className="text-lg font-semibold mb-4">กำลังไฟฟ้ารวม (kW)</h2>
                            <div className="space-y-3">
                                {inputs.kwItems.map((it, index) => (
                                    <div key={it.id} className="grid grid-cols-12 gap-3 items-center">
                                        <input
                                            className="col-span-6 rounded-xl border px-3 py-2 text-sm bg-gray-50"
                                            value={it.name}
                                            onChange={(e) => updateKw(it.id, { name: e.target.value })}
                                            placeholder={`Device Name ${index + 1}`}
                                        />
                                        <input
                                            type="number" step="0.1" min={0}
                                            className="col-span-4 rounded-xl border px-3 py-2 text-sm font-medium"
                                            value={it.kw}
                                            onChange={(e) => updateKw(it.id, { kw: Number(e.target.value) })}
                                        />
                                        <span className="col-span-1 text-sm font-medium">kW</span>
                                        <button 
                                            className="col-span-1 text-red-500 hover:text-red-700 font-bold"
                                            onClick={() => removeKwRow(it.id)}
                                        >&times;</button>
                                    </div>
                                ))}
                                <button onClick={addKwRow} className="px-3 py-2 rounded-xl border bg-gray-900 text-white hover:bg-gray-700 text-sm shadow-sm">+ เพิ่มรายการ</button>
                            </div>
                            <p className="text-sm font-semibold mt-4 p-2 bg-blue-50 rounded-lg">
                                กำลังไฟฟ้ารวมก่อน Load Factor: {num(derived.totalKw)} kW
                            </p>
                        </div>
                        
                        {/* Production & Factors */}
                        <div className="bg-white rounded-2xl shadow p-5">
                            <h2 className="text-lg font-semibold mb-4">พารามิเตอร์การผลิตและอัตราส่วน</h2>
                            <div className="grid md:grid-cols-3 lg:grid-cols-4 gap-4">
                                {/* Row 1: Time */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">นาทีต่อชิ้น (min/piece)</label>
                                    <input
                                        type="number" step="0.01" min={0} className="w-full rounded-xl border px-3 py-2"
                                        value={inputs.minutesPerPiece}
                                        onChange={(e) => update({ minutesPerPiece: Number(e.target.value) })}
                                    />
                                    <p className="text-xs text-gray-500 mt-1">ชิ้นต่อชั่วโมง (Derived): {num(derived.pph)}</p>
                                </div>
                                {/* Row 2: Hours/Day */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">ชั่วโมงการทำงาน/วัน</label>
                                    <input
                                        type="number" min={0} className="w-full rounded-xl border px-3 py-2"
                                        value={inputs.hoursPerDay}
                                        onChange={(e) => update({ hoursPerDay: Number(e.target.value) })}
                                    />
                                </div>
                                {/* Row 3: Days/Month */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">วัน/เดือน</label>
                                    <input
                                        type="number" min={0} className="w-full rounded-xl border px-3 py-2"
                                        value={inputs.daysPerMonth}
                                        onChange={(e) => update({ daysPerMonth: Number(e.target.value) })}
                                    />
                                </div>
                                {/* Row 4: Load Factor */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">Load Factor (%)</label>
                                    <input
                                        type="range" min={0} max={100} step={1} className="w-full mt-3"
                                        value={inputs.loadFactorPct}
                                        onChange={(e) => update({ loadFactorPct: Number(e.target.value) })}
                                    />
                                    <div className="text-sm text-gray-700 mt-1">{inputs.loadFactorPct}%</div>
                                </div>
                                {/* Summary */}
                                <div className="md:col-span-3 lg:col-span-4 p-2 bg-yellow-50 rounded-lg">
                                    <span className="text-sm font-semibold">ชั่วโมงทำงานตาม Load/เดือน: {num(derived.hoursPerMonth)} ชั่วโมง</span>
                                </div>
                            </div>
                        </div>

                        {/* Other cost & Gas */}
                        <div className="bg-white rounded-2xl shadow p-5">
                            <h2 className="text-lg font-semibold mb-4">อัตราต้นทุน (Rates)</h2>
                            <div className="grid md:grid-cols-3 lg:grid-cols-4 gap-4">
                                {/* Elec Rate */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">1. ค่าไฟ/หน่วย (บาท/kWh)</label>
                                    <input
                                        type="number" step="0.01" min={0} className="w-full rounded-xl border px-3 py-2 font-medium"
                                        value={inputs.elecRateTHBPerKwh}
                                        onChange={(e) => update({ elecRateTHBPerKwh: Number(e.target.value) })}
                                    />
                                </div>
                                {/* Spare Cost */}
                                <div>
                                    <label className="block text-sm text-gray-600 mb-1">2. อะไหล่สิ้นเปลือง/เดือน (บาท)</label>
                                    <input
                                        type="number" step="0.01" min={0} className="w-full rounded-xl border px-3 py-2 font-medium"
                                        value={inputs.sparePartsTHBPerMonth}
                                        onChange={(e) => update({ sparePartsTHBPerMonth: Number(e.target.value) })}
                                    />
                                </div>
                                {/* Gas Rate THB/min */}
                                <div className="md:col-span-2">
                                    <label className="block text-sm text-gray-600 mb-1">3. ค่าแก๊ส/นาที (บาท/min)</label>
                                    <input
                                        type="number" step="0.0001" min={0} className="w-full rounded-xl border px-3 py-2 font-medium"
                                        value={inputs.gasRateTHBPerMin}
                                        onChange={(e) => update({ gasRateTHBPerMin: Number(e.target.value) })}
                                    />
                                    <div className="flex gap-2 mt-2">
                                        {(["air", "o2", "n2"] as GasProfileKey[]).map((k) => (
                                            <button
                                                key={k}
                                                className={`px-3 py-1 rounded-xl border shadow-sm text-xs ${inputs.activeGas === k ? "bg-blue-600 text-white" : "bg-white hover:bg-gray-100"}`}
                                                onClick={() => handleGasTypeChange(k)}
                                            >
                                                {k.toUpperCase()}
                                            </button>
                                        ))}
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </section>

                    {/* Right: Results */}
                    <aside className="space-y-6">
                        <div className="bg-white rounded-2xl shadow p-5 sticky top-6">
                            <h2 className="text-lg font-semibold mb-4">สรุปผลการคำนวณ</h2>
                            
                            {/* Derived Production/Machine */}
                            <div className="space-y-2 text-sm border-b pb-3 mb-3">
                                <div className="flex justify-between"><span>kW รวม (ใช้คำนวณ)</span><span className="font-bold text-red-600">{num(derived.totalKwAfterLoad)} kW</span></div>
                                <div className="flex justify-between"><span>นาที/ชิ้น</span><span className="font-semibold">{num(derived.minutesPerPiece)} min</span></div>
                                <div className="flex justify-between"><span>ชิ้น/เดือน (Derived)</span><span className="font-semibold">{num(derived.monthlyPieces)} pcs</span></div>
                            </div>
                            
                            {/* Cost Per Piece */}
                            <h3 className="font-semibold mb-2">ต้นทุนต่อชิ้น (บาท/ชิ้น)</h3>
                            <div className="space-y-1 text-sm border-b pb-3 mb-3">
                                <div className="flex justify-between"><span>ไฟฟ้า</span><span className="font-semibold">฿ {thb(derived.elecPerPiece)}</span></div>
                                <div className="flex justify-between"><span>แก๊ส ({inputs.activeGas.toUpperCase()})</span><span className="font-semibold">฿ {thb(derived.gasPerPiece)}</span></div>
                                <div className="flex justify-between"><span>อะไหล่สิ้นเปลือง</span><span className="font-semibold">฿ {thb(derived.sparePerPiece)}</span></div>
                                <div className="flex justify-between text-base mt-2"><span className="font-bold">รวมต้นทุนต่อชิ้น</span><span className="font-bold text-lg text-green-600">฿ {thb(derived.totalPerPiece)}</span></div>
                            </div>
                            
                            {/* Total Cost / Month */}
                            <h3 className="font-semibold mb-2">ต้นทุนรวม/เดือน (ตาม Load)</h3>
                            <div className="space-y-1 text-sm">
                                <div className="flex justify-between"><span>ค่าไฟ/เดือน</span><span className="font-semibold">฿ {thb(derived.elecPerMonth)}</span></div>
                                <div className="flex justify-between"><span>แก๊ส/เดือน</span><span className="font-semibold">฿ {thb(derived.gasPerMonth)}</span></div>
                                <div className="flex justify-between"><span>อะไหล่สิ้นเปลือง/เดือน</span><span className="font-semibold">฿ {thb(inputs.sparePartsTHBPerMonth)}</span></div>
                                <div className="flex justify-between text-base mt-2">
                                    <span className="font-bold">รวมต่อเดือน</span>
                                    <span className="font-bold text-lg text-red-600">฿ {thb(derived.totalPerMonth)}</span>
                                </div>
                            </div>
                        </div>
                    </aside>
                </div>
            </div>
        </div>
    );
}